var vision = require('@google-cloud/vision');

_global.HTMLCS.providers.gcv = function () {
    var self = {};


    self.isSimilarAlt = async function (altText, imageSource, beStrict, compareFunc) {
        var isSimilar = true;
        try {
            var client = new vision.ImageAnnotatorClient();
            var labels = await client.labelDetection(imageSource);
            isSimilar = compareFunc(labels, altText)
        } catch (e) {
            console.error('HTMLCS.providers.gcv', e);
            if (beStrict) isSimilar = false;
        }


        return isSimilar;
    };


    return self;

}();
